<template>
  <v-layout row wrap>
    <v-flex xs6>
      <v-menu
        :close-on-content-click="false"
        v-model="menus.date"
        lazy
        transition="scale-transition"
        offset-y
        full-width
      >
        <v-text-field
          slot="activator"
          v-model="date"
          :label="label"
          readonly
        />
        <v-date-picker
          v-model="date"
          type="date"
        >
          <v-spacer/>
          <v-btn flat v-text="$t('close')" @click="menus.date = false"/>
        </v-date-picker>
      </v-menu>
    </v-flex>
    <v-flex xs6>
      <v-menu
        :close-on-content-click="false"
        v-model="menus.time"
        lazy
        transition="scale-transition"
        offset-y
        full-width
      >
        <v-text-field
          slot="activator"
          v-model="time"
          :label="$t('time')"
          readonly
        />
        <v-time-picker
          v-model="time"
          format="24hr"
        >
          <v-spacer/>
          <v-btn flat v-text="$t('close')" @click="menus.time = false"/>
        </v-time-picker>
      </v-menu>
    </v-flex>
  </v-layout>
</template>

<script>
export default {
  props: {
    value: String,
    label: String
  },
  data: () => ({
    item: '',
    menus: { date: false, time: false }
  }),
  computed: {
    date: {
      get () {
        return this.item.substr(0, 10)
      },
      set (val) {
        this.item = val + this.item.substr(10)
        this.$emit('input', this.item)
      }
    },
    time: {
      get () {
        return this.item.substr(11)
      },
      set (val) {
        this.item = this.item.substr(0, 11) + val + this.item.substr(16)
        this.$emit('input', this.item)
      }
    }
  },
  watch: {
    value (val) {
      this.item = val || ''
    }
  },
  mounted () {
    this.item = this.value || ''
  }
}
</script>
