<template>
  <v-autocomplete
    :items="items"
    :label="$t('owner')"
    :loading="loading"
    :search-input.sync="search"
    cache-items
    hide-details
    hide-no-data
    item-text="name"
    item-value="subjectId"
    v-model="select"
  />
</template>

<script>
import { searchOwners } from '@/axios'
export default {
  props: {
    value: Object
  },
  data: () => ({
    loading: false,
    search: null,
    items: []
  }),
  computed: {
    select: {
      get () {
        return (this.value || {}).subjectId
      },
      set (value) {
        const item = this.items.find(item => item.subjectId === value)
        this.$emit('input', item)
      }
    }
  },
  watch: {
    search (val) {
      val && val !== (this.value || {}).name && this.querySelections(val)
    }
  },
  methods: {
    async querySelections (v) {
      try {
        this.loading = true
        this.items = await searchOwners(v)
      } catch (error) {
        console.error(error)
      } finally {
        this.loading = false
      }
    }
  }
}
</script>
