import Axios from 'axios'
import store from '@/store'

function deCSharpKeys (data) {
  if (data instanceof Array) {
    return data.map(val => deCSharpKeys(val))
  } else if (data instanceof Object) {
    const fixed = {}
    Object.keys(data).forEach(key => {
      fixed[`${key.slice(0, 1).toLowerCase()}${key.slice(1)}`] = deCSharpKeys(data[key])
    })
    return fixed
  } else {
    return data
  }
}

function reCSharpKeys (data) {
  if (data instanceof Array) {
    return data.map(val => reCSharpKeys(val))
  } else if (data instanceof Object) {
    const fixed = {}
    Object.keys(data).forEach(key => {
      fixed[`${key.slice(0, 1).toUpperCase()}${key.slice(1)}`] = reCSharpKeys(data[key])
    })
    return fixed
  } else {
    return data
  }
}

async function download (config) {
  try {
    store.commit('setWorking', true)
    store.commit('clearAlert')
    return await axios(config)
  } catch (error) {
    store.commit('setAlert', {
      type: 'error',
      text: error.message
    })
    throw error
  } finally {
    store.commit('setWorking', false)
  }
}

const axios = Axios.create({
  baseURL: 'https://inpro-evidencenaklvozu.azurewebsites.net/API/'
})

axios.interceptors.response.use((response) => {
  return {
    ...response,
    data: deCSharpKeys(response.data)
  }
}, error => {
  return Promise.reject(error)
})

async function fetchAllCars () {
  const { data } = await download({
    method: 'get',
    url: '/Car/GetAllCars'
  })
  return data
}

async function fetchAllUsers () {
  const { data } = await download({
    method: 'get',
    url: '/User/GetAllUsers'
  })
  data.forEach(user => {
    user.role = (user.role || {}).roleId
  })
  return data
}

async function addCar (car) {
  return download({
    method: 'post',
    url: '/Car/AddCar',
    headers: {
      'content-type': 'application/json'
    },
    data: reCSharpKeys(car)
  })
}

async function removeCar (car) {
  return download({
    method: 'post',
    url: '/Car/ArchiveCar',
    headers: {
      'content-type': 'application/json'
    },
    data: reCSharpKeys(car)
  })
}

async function addUser (user) {
  return download({
    method: 'post',
    url: '/Admin/AddUser',
    headers: {
      'content-type': 'application/json'
    },
    data: reCSharpKeys({
      ...user,
      role: {
        roleId: user.role
      }
    })
  })
}

async function removeUser (user) {
  throw new Error('Not implemented.')
}

async function searchOwners (search) {
  const { data } = await download({
    method: 'get',
    url: '/Car/GetAllCars'
  })
  const set = new Set()
  return data
    .map(({ owner }) => owner)
    .filter(v => {
      if (v == null) {
        return false
      }
      if (set.has(v.subjectId)) {
        return false
      }
      set.add(v.subjectId)
      return true
    })
}

export default axios
export { fetchAllCars, fetchAllUsers, addCar, removeCar, addUser, removeUser, searchOwners }
